.search-page {
  position: relative;
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  flex-direction: column; /* Ensures that the content stacks vertically */
}

/* Slideshow Container */
.slideshow-container {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  overflow: hidden;
  z-index: -1; /* Ensure it stays behind the search bar */
}

/* Individual Slideshow Image */
.slideshow-image {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-size: cover;
  background-position: center;
  opacity: 0;
  transition: opacity 2s ease-in-out; /* Fade transition */
}

.slideshow-image.active {
  opacity: 1;
}

.search-bar-container {
  z-index: 1; /* Ensure the search bar is above the background image */
  width: 100%; /* Make sure the container takes full width */
  display: flex; /* Use flexbox to align items inside */
  justify-content: center; /* Center the input horizontally */
}

.search-bar-input {
  width: 50%; /* Adjust the width as needed */
  padding: 15px;
  font-size: 18px;
  border: 2px solid #ccc;
  border-radius: 30px; /* Rounded corners */
  outline: none;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  text-align: center;
}