.section-header {
  height: 40vh; /* Adjust the height as needed */
  width: 100%;
  background-size: cover;
  background-position: center;
  display: flex;
  flex-direction: column; /* Change to column to stack h1 and p vertically */
  justify-content: center;
  align-items: center;
  color: black;
  text-align: center; /* Center the text horizontally */
}

.section-header h1 {
  font-family: 'Playfair Display', serif; /* Ensure this font is imported */
  font-size: 2.5rem; /* Adjust the font size as needed */
  font-weight: bold;
  text-transform: uppercase; /* Make the text uppercase */
  margin-bottom: 10px; /* Add some space between title and description */
}

.section-description {
  font-family: 'Avenir Next', sans-serif; /* Ensure this font is imported */
  font-size: 1rem;
  margin-top: 0; /* Ensure there's no extra space */
  margin-bottom: 20px; /* Adjust this as needed */
  color: #555;
}

.section-content {
  padding: 20px;
  text-align: center; /* Center text horizontally in content */
}

.business-item {
  margin-bottom: 20px;
  padding: 10px;
  background-color: #f8f8f8;
  border-radius: 10px;
  text-align: center; /* Center the text horizontally */
}

.business-item h2 {
  font-family: 'Playfair Display', serif; /* Ensure this font is imported */
  font-size: 1.5rem;
  margin-bottom: 10px;
}

.business-item p {
  font-family: 'Avenir Next', sans-serif; /* Ensure this font is imported */
  font-size: 1rem;
  color: #555;
}